
import { isScrollBottom } from "@/utils/comm.js";
import { mapGetters, mapActions, mapMutations } from 'vuex';
import { api } from '@/utils/api.js';
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'

export default {
components: {
XHeader
},
data() {
return {
sc_info: "",
listLoading: true,
isNull: false,
upLoading: false,
upLoadingComplete: false,
page: 2,
nowLoading: false,
lock: false,
isallSelect: false,
isbianji: false,
}
},
created() {
this.showSc();
},
mounted() {
this.$nextTick(()=>{
isScrollBottom(this.onReachBottom);
})
},
destroyed () {
window.onscroll = null;
},
methods: {
async showSc(){
let data = {
plat: 3,
account: this.account,
token: this.token,
page: 1
}
const [err, res] = await api.collectgoodslist(data);
if (err) {
this.$vux.toast.text(err.msg);
return;
}
if(res.code == 2000){
var isNull;
if (res.data.list == "") {
isNull = true;
} else {
isNull = false;
}
for (var i = 0; i < res.data.list.length; i++) {
res.data.list[i].isSelect = false;
}
this.sc_info = res.data.list;
this.listLoading = false;
this.isNull = isNull;
}
},
bianji(){
this.isbianji = !this.isbianji;
},
allSelect(){
for (var i = 0; i < this.sc_info.length; i++) {
this.sc_info[i].isSelect = !this.isallSelect;
}
this.isallSelect = !this.isallSelect;
this.sc_info = this.sc_info;
},
isallSelectFn(){
for (let i = 0; i < this.sc_info.length; i++) {
if(!this.sc_info[i].isSelect){
this.isallSelect = false;
return;
}
}
this.isallSelect = true;
},
switchSelect(index){
this.sc_info[index].isSelect = !this.sc_info[index].isSelect;
this.isallSelectFn();
},
touchend(){
if (this.lock) {
let that = this;

setTimeout(() => {
that.lock = false;
}, 100);
}
},
goInfo(type,goodsid){
if (this.lock) {
return;
}
var url = '/index/goodsInfoPindan';





this.$router.push({
path: url,
query: {
goodsId: goodsid
}
})
},
async onReachBottom(){
if(this.upLoadingComplete)return;
this.nowLoading = true;
this.upLoading = true;
let that = this;
let data = {
plat: 3,
account: this.account,
token: this.token,
page: this.page
}
const [err, res] = await api.collectgoodslist(data);
if (err) {
this.$vux.toast.text(err.msg);
return;
}
if(res.code == 2000){
if (res.data.list != "") {
if (that.isallSelect){
for (var i = 0; i < res.data.list.length; i++) {
res.data.list[i].isSelect = true;
}
}else{
for (var i = 0; i < res.data.list.length; i++) {
res.data.list[i].isSelect = false;
}
}
that.sc_info = that.sc_info.concat(res.data.list);
that.upLoading = false;
that.nowLoading = false;
that.page = that.page + 1;
} else {
that.upLoadingComplete = true;
that.upLoading = true;
that.nowLoading = false;
}
}
},
qxScgoods(){
this.lock = true;
let that = this;
var selectArry = "";
for (var i = 0; i < this.sc_info.length; i++) {
if (this.sc_info[i].isSelect) {
if (selectArry == "") {
selectArry = this.sc_info[i].fav_goods_id;
} else {
selectArry = selectArry + "," + this.sc_info[i].fav_goods_id;
}
}
}
if (selectArry == "") {
this.$vux.toast.text('请选择要取消收藏的商品');
return false;
}
this.$vux.confirm.show({
title: '',
content: '是否要取消收藏？',
async onConfirm(){
let data = {
plat: 3,
account: that.account,
token: that.token,
goods_id: selectArry,
type: 2
}
const [err, res] = await api.cancelcollectgoods(data);
if (err) {
that.$vux.toast.text(err.msg);
return;
}
if(res.code == 2000){
that.$vux.toast.text('取消收藏成功');
that.page = 2;
that.upLoadingComplete = false;
that.isbianji = false;
that.showSc();
}
}
})
}
},
computed: {
...mapGetters(['user', 'account', 'token'])
}
}



